
  .top-bar {
    height: $top-bar-height;
    min-height: $top-bar-height;
    background-color: rgba(#000,0.7);
    top: initial;
    left: initial;
    right: initial;
    position:relative;
    flex: 0 0 100%;
    width: 100%;
    z-index: 2;
    text-align: center;
    user-select: none;
    -webkit-app-region: drag;
  }